@import url(https://fonts.googleapis.com/css?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css?family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Carter+One&display=swap);
.header-container {
  background: #69546b;
  width: 100%;
  text-align: center;
}

.header-title {
  font-family: "Rock Salt", cursive;
  font-size: 2.25rem;
  color: #999900;
  -webkit-animation-name: title-flip;
          animation-name: title-flip;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes title-flip {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(-1, 1);
            transform: scale(-1, 1);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

@keyframes title-flip {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  50% {
    -webkit-transform: scale(-1, 1);
            transform: scale(-1, 1);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

.new-game-button {
  border: none;
  margin-top: 25rem;
  font-size: 3.75rem;
  font-family: "Carter One", cursive;
  background: #69546b;
  color: #999900;
  -webkit-animation-name: button-fade;
          animation-name: button-fade;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

@media screen and (max-width: 1280px) and (max-height: 720px) {
  .new-game-button {
    margin-top: 12.5rem;
  }
}

@media screen and (min-width: 1500px) {
  .new-game-button {
    margin-top: 18.75rem;
  }
}

.new-game-button:hover {
  background: #999900;
  color: #69546b;
  cursor: pointer;
}

.difficulty-container {
  margin-top: 25rem;
  font-size: 3.75rem;
  text-align: center;
}

@media screen and (max-width: 1280px) and (max-height: 720px) {
  .difficulty-container {
    margin-top: 9.375rem;
  }
}

@media screen and (min-width: 1500px) {
  .difficulty-container {
    margin-top: 200px;
  }
}

.difficulty-option {
  border: 1px solid black;
  margin: 0;
  padding: 0 30px;
  font-family: "Carter One", cursive;
  background: #69546b;
  color: #999900;
  -webkit-animation-name: difficulty-fade;
          animation-name: difficulty-fade;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.difficulty-option:hover {
  background: #999900;
  color: #69546b;
  cursor: pointer;
}

@-webkit-keyframes button-fade {
  from {
    background: transparent;
    color: transparent;
  }
  to {
    background: #69546b;
    color: #999900;
  }
}

@keyframes button-fade {
  from {
    background: transparent;
    color: transparent;
  }
  to {
    background: #69546b;
    color: #999900;
  }
}

@-webkit-keyframes difficulty-fade {
  from {
    background: transparent;
    color: transparent;
    border: none;
  }
  to {
    background: #69546b;
    color: #999900;
  }
}

@keyframes difficulty-fade {
  from {
    background: transparent;
    color: transparent;
    border: none;
  }
  to {
    background: #69546b;
    color: #999900;
  }
}

.card-game-container {
  width: 100%;
}

.info-container {
  display: flex;
  font-size: 2.25rem;
  justify-content: space-evenly;
  background: #999900;
  color: #33827b;
  font-family: "Carter One", cursive;
  text-align: center;
}

.info-difficulty {
  margin: 0;
  width: 33.3333%;
}

.info-moves {
  margin: 0;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 0 0.625rem;
  width: 33.33%;
}

.info-time {
  margin: 0;
  width: 33.33%;
}

.cards-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 15px;
  row-gap: 15px;
  justify-items: center;
  margin-top: 3.125rem;
}

@media screen and (max-width: 1280px) and (max-height: 720px) {
  .cards-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-row-gap: 0.9375rem;
    row-gap: 0.9375rem;
    justify-items: center;
    margin-top: 1.25rem;
  }
}

.cards-easy .card-flip-container,
.cards-easy .card-front,
.cards-easy .card-back {
  width: 9.375rem;
  height: 12rem;
  border-radius: 1.25rem;
  font-size: 2.25rem;
}

.card-flip-container {
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.cards-medium .card-flip-container,
.cards-medium .card-front,
.cards-medium .card-back {
  width: 7.2rem;
  height: 7.5rem;
  border-radius: 1.25rem;
  font-size: 1.8rem;
}

.cards-hard .card-flip-container,
.cards-hard .card-front,
.cards-hard .card-back {
  width: 4.6rem;
  height: 4.8rem;
  border-radius: 1.25rem;
  font-size: 1.5rem;
}

.card-flipper {
  -webkit-transition: 0.6s;
  transition: 0.6s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;

  position: relative;
}

.card-front,
.card-back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-front {
  z-index: 2;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  background: #69546b;
  color: #999900;
  border: 2px solid #999900;
}

.card-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  background: #999900;
  color: #69546b;
  border: 2px solid #69546b;
}

.card-icon {
  pointer-events: none;
}

.card-flip-container.card-flip .card-flipper {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.pair-found {
  background: darkgreen;
  color: #999900;
  border: 2px solid #999900;
}

.won-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2.25rem;
  font-family: "Carter One", cursive;
  margin-top: 6.25rem;
  color: #999900;
}

@media screen and (max-width: 1280px) and (max-height: 720px) {
  .won-container {
    margin-top: 2.5rem;
  }
}

.trophy {
  font-size: 12.5rem;
  margin-bottom: 1.25rem;
}

.won-description {
  margin: 0;
}

.lost-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2.25rem;
  color: black;
  font-family: "Carter One", cursive;
  margin-top: 6.25rem;
}

@media screen and (max-width: 1280px) and (max-height: 720px) {
  .lost-container {
    margin-top: 2.5rem;
  }
}

.thumbs-up {
  font-size: 12.5rem;
  margin-bottom: 1.25rem;
}

.lost-description {
  margin: 0;
}

.start-new-game-again {
  margin-top: 0.625rem;
  font-size: 2.25rem;
  font-family: "Carter One", cursive;
  background: #69546b;
  color: #999900;
  border: none;
  border-radius: 20px;
}

.start-new-game-again:hover {
  background: #999900;
  color: #69546b;
  cursor: pointer;
}

@media screen and (min-width: 1500px) {
  .cards-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-row-gap: 0.9375rem;
    row-gap: 0.9375rem;
    justify-items: center;
    margin-top: 3.125rem;
  }
}

@media screen and (orientation: landscape) {
  .cards-medium .card-flip-container,
  .cards-medium .card-front,
  .cards-medium .card-back {
    width: 8rem;
    height: 9rem;
    border-radius: 1.25rem;
    font-size: 2.25rem;
  }

  .cards-hard .card-flip-container,
  .cards-hard .card-front,
  .cards-hard .card-back {
    width: 5rem;
    height: 6.5rem;
    border-radius: 1.25rem;
    font-size: 2.25rem;
  }
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #33827b;
}

html,
body {
  font-size: 10px;
}

@media screen and (orientation: landscape) {
  html,
  body {
    font-size: 16px;
  }
}

